<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6>Data Form Peserta</h6>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <form role="form" @submit.prevent="submit">
      <div class="table-responsive p-0 overflow-auto">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Keterangan</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Gambar</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Preview Gambar</th>
            </tr>
          </thead>
          <tbody v-if="data.user">
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Tiket Transportasi</td>
              <td class="align-middle text-center text-sm">
                <img :src="(data.user.tiket !=='0') ? ('http://127.0.0.1:8000/storage/' + data.user.tiket) :  ('http://127.0.0.1:8000/storage/kendaraanpribadi.jpg')" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.tiket">Lihat Tiket</a></td>
            </tr>                  
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Surat Izin</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.surat_izin" alt="Surat Izin" height="100" class="img-modal-izin">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.surat_izin">Lihat Surat</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Konfirmasi Kedatangan</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.konfirmasi" alt="Konfirmasi Kedatangan" height="100" class="img-modal-konfirmasi">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.konfirmasi">Lihat Konfirmasi</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Nota Kesepakatan</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.nota" alt="Tiket Transportasi" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.nota">Lihat Kesepakatan</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Bukti Transfer</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.transfer" alt="Tiket Transportasi" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.transfer">Lihat Bukti TF</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">KTM</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.ktm" alt="Tiket Transportasi" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.ktm">Lihat KTM</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Surat SPPD</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.sppd" alt="Tiket Transportasi" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.sppd">Lihat SPPD</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Foto dengan Almamater</td>
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/' + data.user.photo" alt="Tiket Transportasi" height="100">
              </td>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"><a :href="'http://127.0.0.1:8000/storage/' + data.user.photo">Lihat Foto</a></td>
            </tr>
            <tr>
              <td class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Aktivkan akun</td>
              <td class="align-middle text-center text-sm">
                <input type="checkbox" name="example" value="option1" v-model="verified" class="custom-checkbox"><br>
              </td>
              <td class="align-middle text-center">
                <span :class="data.user.verified ? 'badge badge-sm bg-gradient-primary' : 'badge badge-sm bg-gradient-secondary'">
                  {{ data.user.verified ? 'Aktif' : 'Belum Aktif' }}
                </span> 
              </td> 
            </tr>
          </tbody>
          <tbody v-else>
            <tr>
              <td><p>Belum ada data</p></td>
            </tr>
          </tbody>
        </table>
        <div class="modal fade" id="modal-image" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
          <div class="modal-dialog modal-lg">
            <div class="modal-content">
              <div class="modal-body">
                <img :src="imageUrl" :alt="imageAlt" class="img-fluid">
              </div>
            </div>
          </div>
        </div>
        <div class="col-6 text-home px-4 pb-4">
          <argon-button color="dark" variant="gradient"  @click="submit">
            Simpan
          </argon-button>
        </div>
       </div>
      </form>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ArgonButton from "@/components/ArgonButton.vue";
import axios from 'axios';

export default {
  name: "formdata",
  props: {
    id: {
      type: Number,
      required: true
    }
  },
  components: {
    ArgonButton,
  },
  data() {
    return {
      imageUrl: '',
      imageAlt: '',
      data: {
         user:{
            name: '',
            email: '',
            phone: '',
            tiket: '',
            photo: '',
            ktm: '',
            transfer: '',
            verified: '',
        } 
      }
    } 
  },
  mounted() {
    axios.get(`http://127.0.0.1:8000/api/users/${this.$route.params.id}`)
    .then(response => {
      this.data.user = response.data.data;
    })
    .catch(error => {
      console.error(error);
    });
  },
  methods: {
      submit() {
        const url = `http://127.0.0.1:8000/api/verified/${this.$route.params.id}`;
        const data = {
          verified: this.verified ? 1 : 0,
        };

        axios.post(url, data)
          .then(response => {
            console.log(response);
            // handle response here
            this.$router.push('/validasiAkun'); // Navigate to "/logistik" path
          })
          .catch(error => {
            console.log(error);
            // handle error here
          });
      }
    },

};
</script>
